import React from "react";
import { Card } from "../../Common/components/Card/Card";
import styled from "styled-components";
import { PrimaryButton } from "../../Common/components/Buttons/PrimaryButton";
import Input from "../../Common/components/Input/Input";
import { LinkText } from "../../Common/components/LinkText/LinkText";
import Radio from "../../Common/components/Radio/Radio";
import DatePicker from "react-datepicker";

import "react-datepicker/dist/react-datepicker.css";
import DateInput from "../../Common/components/DateInput/DateInput";

const parseDate = (str) => {
  let date = new Date(str),
    mnth = ("0" + (date.getMonth() + 1)).slice(-2),
    day = ("0" + date.getDate()).slice(-2);
  return [date.getFullYear(), mnth, day].join("-");
};
const Title = styled.h1`
  margin: 0;
  text-align: center;
`;

const InputWrapper = styled.div`
  margin: 10px;
  display: flex;
  flex-direction: column;
`;

const SignupButton = styled(PrimaryButton)`
  margin-top: 20px;
  width: 100%;
`;

const Row = styled.div`
  display: grid;
  grid-gap: 10px;
  grid-template-columns: 180px 180px;
`;

const BackLink = styled(LinkText)`
  text-align: center;
  margin-top: 10px;
`;

const GENDER_OPTIONS = [
  { id: "male", value: "Male" },
  { id: "female", value: "Female" },
];

function BasicDetails({
  handleContinue,
  handleChange,
  values,
  handleBack,
  setFieldValue,
}) {
  const handleDateChange = (date) => {
    const dateString = parseDate(date);
    setFieldValue("date_of_birth", dateString);
  };
  return (
    <>
      <Card width={450}>
        <Title>Basic Details</Title>
        <InputWrapper>
          <Row>
            <Input
              label="Date of Birth"
              type="text"
              name="date_of_birth"
              value={values.date_of_birth}
              placeholder="YYYY-MM-DD"
              onChange={handleChange}
            />
            {/* <DateInput
              placeholder="YYYY-MM-DD"
              value={values.date_of_birth}
              onChange={handleDateChange}
            /> */}
            <Radio
              label="Gender"
              options={GENDER_OPTIONS}
              selected={values.gender}
              name="gender"
              onChange={handleChange}
            />
          </Row>
          <Row>
            <Input
              label="Height (in ft, inch)"
              type="text"
              name="height"
              value={values.height}
              placeholder="Enter your height"
              onChange={handleChange}
            />
            <Input
              label="Weight (in Kgs)"
              type="text"
              name="weight"
              value={values.weight}
              placeholder="Enter your weight"
              onChange={handleChange}
            />
          </Row>
          <Row>
            <Input
              label="Education"
              type="text"
              name="education"
              value={values.education}
              placeholder="Enter your qualification"
              onChange={handleChange}
            />
            <Input
              label="City"
              type="text"
              name="city"
              value={values.city}
              placeholder="Enter your city"
              onChange={handleChange}
            />
          </Row>
          <SignupButton onClick={handleContinue}>Continue</SignupButton>
          <BackLink onClick={handleBack}>Back</BackLink>
        </InputWrapper>
      </Card>
    </>
  );
}

export default BasicDetails;
