import React from "react";
import styled from "styled-components";
import Dropdown from "react-dropdown";
import "react-dropdown/style.css";

const Container = styled.div`
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
`;

const Label = styled.label`
  font-size: 12px;
  margin-bottom: 6px;
`;

const StyledDropdown = styled(Dropdown)`
  .Dropdown-control {
    padding: 12px 20px 12px 16px;
    background: #ffedf1;
    border: none;
    outline: none;
    border-radius: 0;
    font-size: 13.33px;
    line-height: 16px;
    :hover {
      box-shadow: none;
    }
  }
  .Dropdown-option.is-selected {
    background: #ff4773;
    color: white;
  }
`;

function DropdownReact({ label, options, value, placeholder, onChange, name }) {
  const handleChange = (selected) => {
    onChange && onChange({ target: { ...selected, name } });
  };
  return (
    <Container>
      <Label>{label}</Label>
      <StyledDropdown
        options={options}
        onChange={handleChange}
        value={value}
        placeholder={placeholder}
      />
    </Container>
  );
}

export default DropdownReact;
