import React from "react";
import { connect } from "react-redux";
import styled from "styled-components";
import {
  PrimaryTextButton,
  PrimaryButton,
  PrimaryOutlineButton,
} from "../../Common/components/Buttons/PrimaryButton";
import { userActions } from "../../redux/Login/actions";
import { useSelector } from "react-redux";

const Container = styled.div`
  position: relative;
  -webkit-box-shadow: 0px 5px 40px rgba(185, 185, 185, 0.25);
  -moz-box-shadow: 0px 5px 40px rgba(185, 185, 185, 0.25);
  box-shadow: 0px 5px 40px rgba(185, 185, 185, 0.25);
  border-radius: 16px;
  padding: 20px;
  margin-bottom: 20px;
  display: flex;
`;

const Picture = styled.img`
  border-radius: 8px;
  height: 120px;
  width: 90px;
  object-fit: cover;
`;

const Details = styled.div`
  margin-left: 20px;
`;

const Title = styled.h2`
  margin: 0 0 6px 0;
  font-size: 18px;
  font-weight: 500;
`;

const Label = styled.h3`
  margin: 0 0 3px 0;
  font-size: 14px;
  font-weight: 400;
`;

const ActionButtons = styled.div`
  display: flex;
  flex-direction: column;
  position: absolute;
  bottom: 10px;
  right: 10px;
`;

function ProfileCard({
  age,
  height,
  occupation,
  community,
  caste,
  education,
  user_id,
  approveProfile,
  is_approved,
}) {
  const { approving } = useSelector((state) => {
    return { ...state.authentication };
  });
  return (
    <Container>
      <Picture
        src={
          "https://expertphotography.com/wp-content/uploads/2018/10/cool-profile-pictures-retouching-1.jpg"
        }
      />
      <Details>
        <Title>User Id : JSB819432</Title>
        <Label>
          Age, Height: {age}, {height}
        </Label>
        <Label>
          Religion, Cast: {community}, {caste}
        </Label>
        <Label>Profession: {occupation}</Label>
        <Label>Education: {education}</Label>
      </Details>
      <ActionButtons>
        {!is_approved && (
          <PrimaryButton
            isLoading={approving && approving === user_id}
            onClick={() => {
              approveProfile(user_id);
            }}
          >
            Approve
          </PrimaryButton>
        )}
        <PrimaryOutlineButton>Reject</PrimaryOutlineButton>
        <PrimaryTextButton>View Profile</PrimaryTextButton>
      </ActionButtons>
    </Container>
  );
}

const mapDispatchToProps = {
  approveProfile: userActions.approveProfile,
};

export default connect(null, mapDispatchToProps)(ProfileCard);
