import React from "react";
import styled from "styled-components";
import { useFileSelect } from "../../hooks/useFileSelect";

const Container = styled.div`
  width: 100%;
`;

const Label = styled.label`
  font-size: 12px;
`;

const FileWrapper = styled.label`
  cursor: pointer;
  position: relative;
  width: 100%;
  height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: auto;
  background: rgba(255, 237, 241, 1);
`;

const FileBrowse = styled.input.attrs({ type: "file", id: "file-upload" })`
  display: none;
`;

const FileName = styled.h4``;

const Text = styled.h4`
  font-size: 12px;
  font-weight: 400;
`;

function FileUpload({ label, name }) {
  const [selectedFile, preview, handleFileChange] = useFileSelect();
  return (
    <Container>
      <Label>{label}</Label>
      <FileBrowse onChange={handleFileChange} />
      <FileWrapper htmlFor="file-upload">
        {(preview || name) && <FileName>{preview || name}</FileName>}
        <Text>{preview || name ? "Change File" : "Choose File"}</Text>
      </FileWrapper>
    </Container>
  );
}

export default FileUpload;
