import React from "react";
import styled from "styled-components";
import { PrimaryButton } from "../Common/components/Buttons/PrimaryButton";
import Input from "../Common/components/Input/Input";
import { ReactComponent as Lock } from "../Common/assets/Icon/lock.svg";
import { ReactComponent as Phone } from "../Common/assets/Icon/phone.svg";
import { LinkText } from "../Common/components/LinkText/LinkText";
import { Card } from "../Common/components/Card/Card";
import { connect } from "react-redux";
import { userActions } from "../redux/Login/actions";
import { withFormik } from "formik";
import { useSelector } from "react-redux";

const InputWrapper = styled.div`
  margin: 10px;
  display: flex;
  flex-direction: column;
`;

const LoginButton = styled(PrimaryButton)`
  margin-top: 20px;
  width: 100%;
`;

const Title = styled.h1`
  margin: 0;
  text-align: center;
`;

const ForgotPassword = styled(LinkText)`
  margin-left: auto;
`;

const Text = styled.h3`
  text-align: center;
  font-weight: 400;
  font-size: 13px;
`;

function Login({ values, handleSubmit, handleChange }) {
  const loggingIn = useSelector((state) => {
    return state.authentication.loggingIn;
  });
  return (
    <Card width={400}>
      <Title>Login</Title>
      <InputWrapper>
        <Input
          label="Phone"
          type="text"
          name="phone_no"
          onChange={handleChange}
          value={values.phone_no}
          placeholder="Enter phone number"
          icon={<Phone />}
        />
        <Input
          label="Password"
          type="password"
          name="password"
          value={values.password}
          onChange={handleChange}
          placeholder="Enter password"
          icon={<Lock />}
        />
        <ForgotPassword>Forgot Password?</ForgotPassword>
        <LoginButton isLoading={loggingIn} onClick={handleSubmit}>
          Login
        </LoginButton>
        <Text>
          Dont have an account? <LinkText to="/signup">Signup</LinkText>
        </Text>
      </InputWrapper>
    </Card>
  );
}

const LoginFormik = withFormik({
  mapPropsToValues: () => ({ phone_no: "", password: "" }),
  handleSubmit: (values, { props }) => {
    props.login(
      values.phone_no,
      values.password,
      (user) => {
        console.log(user.data[0]);
        if (user.data[0].is_admin) {
          props.history.push("/admin/profiles");
        } else {
          props.history.push("/matches");
        }
      },
      (err) => {}
    );
  },
})(Login);

const mapDispatchToProps = {
  login: userActions.login,
};

const ConnectedLoginFormik = connect(null, mapDispatchToProps)(LoginFormik);

export default ConnectedLoginFormik;
